<template>
  <section id="Galeria" class="section">
    <div class="contenedor">
      <div class="principal">
        <img class="imagen-principal holder" />
      </div>
      <div class="contenedor-galeria">
        <div class="galeria">
          <!--     IMAGEN     -->
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder activo" />
          </div>
          <!--     IMAGEN     -->
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
          <!--     IMAGEN     -->
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
          <!--     IMAGEN     -->
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
          <!--     IMAGEN     -->
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
           
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
          <!--
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
         
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
         
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>
           
          <div class="contenedor-imagen">
            <img class="imagen-galeria holder" />
          </div>  -->
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import image1 from '../assets/img/galeria/g1.jpg'
import image2 from '../assets/img/galeria/g2.jpg'
import image3 from '../assets/img/galeria/g3.jpg'
import image4 from '../assets/img/galeria/g4.jpg'
import image5 from '../assets/img/galeria/g5.jpg'
import image6 from '../assets/img/galeria/g6.jpg'
import image7 from '../assets/img/galeria/g7.jpg'
import image8 from '../assets/img/galeria/g8.jpg'
import image9 from '../assets/img/galeria/g9.jpg'
import image10 from '../assets/img/galeria/g10.jpg'

export default {
  name: 'GaleriaComp',
  data() {
    return {
      imagenes: [
        image1,
        image2,
        image3,
        image4,
        image5,
        image6
      ],
    }
  },
  mounted() {
    const galeria = document.getElementsByClassName('galeria')[0]
    const imagenesGaleria = document.getElementsByClassName('imagen-galeria')
    const imagenPrincipal = document.getElementsByClassName('imagen-principal')

    const imagenActiva = (elem) => {
      if (elem.target.classList.contains('activo')) {
        return
      } else {
        imagenPrincipal[0].src = elem.target.src

        for (let i = 0; i < imagenesGaleria.length; i++) {
          if (imagenesGaleria[i].classList.contains('activo')) {
            imagenesGaleria[i].classList.remove('activo')
          }
        }

        elem.target.classList.add('activo')
      }
    }

    //          MOVIMIENTO SIEMPRE DERECHA       //
    let intervalo
    const scrollRate = 0.2
    let izquierda
    const widthContenedorImagen = 23

    const wrap = () => {
      const ultimo = galeria.children[galeria.children.length - 1]

      galeria.removeChild(ultimo) //creo que esta linea no es necesaria
      galeria.insertBefore(ultimo, galeria.children[0])
      galeria.style.left = `-${widthContenedorImagen}%`
    }

    const moverDerecha = () => {
      if (izquierda > -widthContenedorImagen && izquierda < 0) {
        izquierda = izquierda - widthContenedorImagen

        const ultimo = galeria.children[galeria.children.length - 1]
        galeria.removeChild(ultimo)
        galeria.style.left = `${izquierda}%`
        galeria.insertBefore(ultimo, galeria.children[0])
      }

      izquierda = izquierda || widthContenedorImagen

      intervalo = setInterval(() => {
        galeria.style.left = `${izquierda}%`

        if (izquierda < 0) {
          izquierda += scrollRate
        } else {
          izquierda = -widthContenedorImagen
          wrap()
        }
      }, 1)
    }

    const detenerMovimiento = () => {
      clearInterval(intervalo)
    }

	////           INICIACION         ////
    moverDerecha()
    galeria.addEventListener('mouseenter', detenerMovimiento)
    galeria.addEventListener('mouseleave', moverDerecha)
    
    imagenPrincipal[0].src = this.imagenes[0]

    for (let i = 0; i < imagenesGaleria.length; i++) {
      imagenesGaleria[i].src = this.imagenes[i]
      imagenesGaleria[i].addEventListener('click', imagenActiva)
    }
  },
}
</script>

