    </div>
    <div class="navbar">
      <div id="logo-nav">
        <a href="#Header"><img src="../assets/img/logo.jpg" alt="logo" /></a>
      </div>
      <ul class="lista-navbar">
        <li><a href="#Galeria">Galeria</a></li>
        <li><a href="#Servicios">Servicios</a></li>
        <li><a href="#Nosotros">Nosotros</a></li>
        <li><a href="#Contacto">Contacto</a></li>
      </ul>
      <div class="menu">
        <img  id="imagen-lista" src="../assets/icon/lista.png" alt="menu" @click.prevent="toggle()"/>
        <div id="desplegable" class="lista-menu">
          <ul>
            <li><a href="#Galeria">Galeria</a></li>
            <li><a href="#Servicios">Servicios</a></li>
            <li><a href="#Nosotros">Nosotros</a></li>
            <li><a href="#Contacto">Contacto</a></li>
          </ul>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import abrir from '../assets/icon/lista.png'
import cerrar from '../assets/icon/cerrar.png'

export default {
  name: 'NavBar',
  methods: {
    toggle() {
      const imagen = document.getElementById('imagen-lista').src
      const listaMenu = document.getElementById('desplegable')
      const main = document.getElementById('cuerpo')

      listaMenu.classList.add('active-menu')
      if (listaMenu.classList.contains('active-menu')) {
              main.addEventListener('click', () => {
                listaMenu.classList.remove('active-menu')
                document.getElementById('imagen-lista').src = abrir
              })
      }
            
      if (imagen == abrir) {
        document.getElementById('imagen-lista').src = cerrar
        listaMenu.classList.add('active-menu')
      } else {
        document.getElementById('imagen-lista').src = abrir
        listaMenu.classList.remove('active-menu')
      }

      //listaMenu.classList.toggle('active-menu')     
    },
  },
}
</script>

