<script>
export default {
  name: 'ContactoComp',
  data() {
    return {
      contacto: {
        nombre: '',
        telefono: '',
        correo: '',
        mensaje: '',
      },
      //mensajes
      mensajeRellenarCampo: null,
      mensajeNombreInvalido: null,
      mensajeTelefonoInvalido: null,
      mensajeCorreoInvalido: null,
      mensajeMensajeInvalido: null,
    }
  },
  methods: {
    enviar() {
      //Verificacion
      if (
        this.contacto.nombre === '' ||
        this.contacto.telefono === '' ||
        this.contacto.correo === '' ||
        this.contacto.mensaje === ''
      ) {
        this.mensajeRellenarCampo = '*Por favor, rellene todos los campos.'
      } else {
        alert('Enviado')
        this.mensajeRellenarCampo = ''
      }
    },
  },
}
</script>

