<template>
  <div>
    <VectoresFondo class="fondo-vectorial desactivado" />
    <div :style="bgImage" class="fondo-phone activado"></div>
    <img
      @click="haciaArriba()"
      class="flecha-arriba"
      id="boton"
      src="../src/assets/icon/chevron-flecha-hacia-arriba.png"
    />
    <NavBar />
    <div id="cuerpo">
      <HeaderPortada />
    <main>
      <GaleriaComp />
      <ServiciosComp />
      <NosotrosComp />
      <ContactoComp />
    </main>
    <footer>
      <p>© tecnim. Todos los derechos reservados.</p>
      <p>
        Background by
        <a href="https://github.com/JayexDesigns/particles-background"
          >JayexDesigns</a
        >
      </p>
    </footer>
    </div>
    
  </div>
</template>

<script>
import NavBar from '@/components/NavBar.vue'
import HeaderPortada from '@/components/HeaderPortada.vue'
import GaleriaComp from '@/components/GaleriaComp.vue'
import ServiciosComp from '@/components/ServiciosComp.vue'
import NosotrosComp from '@/components/NosotrosComp.vue'
import ContactoComp from '@/components/ContactoComp.vue'
import VectoresFondo from '@/components/VectoresFondo.vue'

export default {
  name: 'App',
  data() {
    return {
      bgImage: {
        backgroundImage: `url(${require('../src/assets/img/fondo.png')})`
        }
    }
  },
  components: {
    NavBar,
    HeaderPortada,
    GaleriaComp,
    ServiciosComp,
    NosotrosComp,
    ContactoComp,
    VectoresFondo,
  },
  mounted() {
    'use strict'
    /*
    const vw = Math.max(
      document.documentElement.clientWidth || 0,
      window.innerWidth || 0
    )
    const vh = Math.max(
      document.documentElement.clientHeight || 0,
      window.innerHeight || 0
    )
    */
    const boton = document.getElementById('boton')
    const logo = document.getElementById('logo-nav')

    ////// SPYSCROLL ///////
    const sections = document.querySelectorAll('.section')

    window.onscroll = () => {
      ////// BOTON DE DESPLASAMIENTO HACIA ARRIBA ///////
      if (
        document.body.scrollTop > 100 ||
        document.documentElement.scrollTop > 100
      ) {
        boton.style.display = 'block'
        boton.style.opacity = '1'
        boton.style.transition = 'opacity 1.5s'
      } else {
        boton.style.display = 'none'
        boton.style.opacity = '0'
        boton.style.transition = 'opacity 1.5s'
      }
      ////// LOGO EN NAVBAR ///////
      if (
        document.body.scrollTop > 400 ||
        document.documentElement.scrollTop > 400
      ) {
        logo.style.opacity = '1'
        logo.style.transition = 'opacity 0.5s'
      } else {
        logo.style.opacity = '0'
        logo.style.transition = 'opacity 0.5s'
      }
      ////// SPYSCROLL ///////
      let scrollPosition =
        document.documentElement.scrollTop || document.body.scrollTop

      for (let s in sections) {
        if (
          sections.hasOwnProperty(s) &&
          sections[s].offsetTop <= scrollPosition
        ) {
          const id = sections[s].id
          document
            .querySelector('.active-navbar')
            .classList.remove('active-navbar')
          document
            .querySelector(`a[href*=${id}]`)
            .parentNode.classList.add('active-navbar')
        }
      }
    }
  },
  methods: {
    haciaArriba() {
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    },
  },
}
</script>

