  height: 150px;
  margin-top: 200px;
}
footer p {
  text-align: center;
  width: 100%;
  margin: 0;
}
footer a {
  color: white;
  text-decoration: none;
}

/*     FLECHA HACIA ARRIBA      */
.flecha-arriba {
  display: none;
  width: 50px;
  position: fixed;
  bottom: 50px;
  left: 50px;
  z-index: 99;
  cursor: pointer;
}

.fondo-vectorial {
  position: fixed;
  z-index: -100;
  width: 100%;
}
.fondo-phone {
  background-repeat: repeat;
  background-size: auto;
  position: fixed;
  z-index: -50;
  width: 100%;
}
