<template>
  <div onload="changeCanvasColor()">
    <canvas id="particlesCanvas"></canvas>
  </div>
</template>

<script>
export default {
  name: 'vectoresFondo',
  mounted() {
    ///////// BACKGROUND VECTORIAL BY JayexDesigns////////
    //https://github.com/JayexDesigns/particles-background
    
    var showFPS = false
    var fpsLimit = 60

    var transparentBackground = false
    var backgroundColor1 = '#ffffff'
    var backgroundColor2 = '#ffffff'

    var particleColor = '#C9C7C7'
    var lineColor = '#C9C7C7'

    var particleQuantity = 150

    var particleRadius = 3
    var lineWidth = 0.5

    var minVelocity = 0.5
    var maxVelocity = 2

    // Advanced Settings, Change Them At Your Own Risk
    var disapearOffset = 150 // Distance At Which The Particles Disapear (Leave It Equal To The maxDistOpacity For Smooth Transition)
    var minDistOpacity = 50 // Distance Between Particles At Which The Line Starts To Fade
    var maxDistOpacity = 150 // Distance Between Particles At Which The Line Is No Longer Visible

    /////////  Class Vector2D
    class Vector2D {
      constructor(x, y) {
        this.x = x
        this.y = y
      }

      //Adds A Vector To The Instance Vector
      add(vector) {
        let x = this.x + vector.x
        let y = this.y + vector.y
        return new Vector2D(x, y)
      }

      //Subtracts A Vector To The Instance Vector
      sub(vector) {
        let x = this.x - vector.x
        let y = this.y - vector.y
        return new Vector2D(x, y)
      }

      //Multiplies The Vector By A Scalar Or Another Vector
      mul(arg) {
        if (arg instanceof Vector2D) {
          let x = this.x * arg.x
          let y = this.y * arg.y
          return new Vector2D(x, y)
        } else {
          let x = this.x * arg
          let y = this.y * arg
          return new Vector2D(x, y)
        }
      }

      //Divides The Vector By A Scalar
      div(num) {
        let x = this.x / num
        let y = this.y / num
        return new Vector2D(x, y)
      }

      //Returns The Module Of The Vector
      abs() {
        return Math.sqrt(this.x ** 2 + this.y ** 2)
      }

      //Calculates The Distance Of Two Points
      dist(vector) {
        let vec = this.sub(vector)
        return vec.abs()
      }

      //Calculates The Distance Of Two Points Squared (More Efficient) Thanks Two Quid ^^
      squaredDist(vector) {
        let vec = this.sub(vector)
        return vec.x ** 2 + vec.y ** 2
      }

      //Returns A Vector That Has The Same Direction As The Instance But Has Module 1
      norm() {
        return new Vector2D(this.x / this.abs(), this.y / this.abs())
      }

      //Returns The Max Value Of A Vector, Max Value Specified By The Parameter
      max(num) {
        let x = (num * this.x) / this.abs()
        let y = (num * this.y) / this.abs()
        return new Vector2D(x, y)
      }
    }

    ///// SCRIPT CANVAS
    var canvas = document.getElementById('particlesCanvas')
    var ctx = canvas.getContext('2d')

    var stats

    const changeCanvasColor = () => {
      if (transparentBackground) {
        canvas.style.backgroundColor = 'rgba(0,0,0,0)'
      } else {
        canvas.style.backgroundImage = `radial-gradient(circle, ${backgroundColor1} 0%, ${backgroundColor2} 100%)`
      }
    }

    const setCanvasSize = () => {
      canvas.width = innerWidth
      canvas.height = innerHeight
    }
    setCanvasSize()
    window.addEventListener('resize', () => {
      setCanvasSize()
    })

    class Particle {
      static particles = []

      constructor() {
        this.radius = particleRadius
        this.pos = new Vector2D(
          Math.floor(
            Math.random() * (innerWidth - this.radius - this.radius) +
              this.radius
          ),
          Math.floor(
            Math.random() * (innerHeight - this.radius - this.radius) +
              this.radius
          )
        )
        this.vel = new Vector2D(Math.random() * 1, Math.random() * 1)
        this.vel.x =
          Math.floor(Math.random() * 2) === 0 ? this.vel.x * -1 : this.vel.x
        this.vel.y =
          Math.floor(Math.random() * 2) === 0 ? this.vel.y * -1 : this.vel.y
        this.vel = this.vel
          .norm()
          .mul(Math.random() * (maxVelocity - minVelocity) + minVelocity)

        Particle.particles.push(this)
      }

      move() {
        this.pos = this.pos.add(this.vel)
        if (
          this.pos.x - this.radius - disapearOffset >= innerWidth &&
          this.vel.x > 0
        ) {
          this.pos.x = 0 - this.radius - disapearOffset
        } else if (
          this.pos.x + this.radius + disapearOffset <= 0 &&
          this.vel.x < 0
        ) {
          this.pos.x = innerWidth + this.radius + disapearOffset
        }
        if (
          this.pos.y - this.radius - disapearOffset >= innerHeight &&
          this.vel.y > 0
        ) {
          this.pos.y = 0 - this.radius - disapearOffset
        } else if (
          this.pos.y + this.radius + disapearOffset <= 0 &&
          this.vel.y < 0
        ) {
          this.pos.y = innerHeight + this.radius + disapearOffset
        }
      }

      print() {
        if (lineWidth > 0) {
          this.connections()
        }
        if (particleRadius > 0) {
          ctx.beginPath()
          ctx.arc(this.pos.x, this.pos.y, this.radius, 0, Math.PI * 2, true)
          ctx.fillStyle = particleColor
          ctx.fill()
        }
        this.move()
      }

      connections() {
        for (let i = 0; i < Particle.particles.length; ++i) {
          if (Particle.particles[i] !== this) {
            let distance = this.pos.squaredDist(Particle.particles[i].pos)
            if (distance < maxDistOpacity ** 2) {
              ctx.beginPath()
              ctx.moveTo(this.pos.x, this.pos.y)
              ctx.lineTo(
                Particle.particles[i].pos.x,
                Particle.particles[i].pos.y
              )
              ctx.lineWidth = lineWidth
              ctx.strokeStyle = `rgba(${parseInt(
                lineColor.slice(1, 3),
                16
              )}, ${parseInt(lineColor.slice(3, 5), 16)}, ${parseInt(
                lineColor.slice(5, 7),
                16
              )}, ${
                distance <= minDistOpacity ** 2
                  ? 1
                  : slope * Math.sqrt(distance) + height
              })`
              ctx.stroke()
            }
          }
        }
      }
    }

    var reqAnim
    var slope
    var height
    const start = () => {
      slope = 1 / (minDistOpacity - maxDistOpacity)
      height = -slope * minDistOpacity + 1

      Particle.particles = []
      for (let i = 0; i < particleQuantity; ++i) {
        new Particle()
      }
      var last = performance.now() / 1000
      var fpsThreshold = 0
      const animate = () => {
        reqAnim = requestAnimationFrame(animate)

        var now = performance.now() / 1000
        var dt = Math.min(now - last, 1)
        last = now
        if (fpsLimit > 0) {
          fpsThreshold += dt
          if (fpsThreshold < 1.0 / fpsLimit) {
            return
          }
          fpsThreshold -= 1.0 / fpsLimit
        }

        ctx.clearRect(0, 0, innerWidth, innerHeight)
        for (let i = 0; i < Particle.particles.length; ++i) {
          Particle.particles[i].print()
        }
        if (showFPS && stats) {
          stats.update()
        } else if (showFPS) {
          stats = createStats()
          stats.domElement.classList.add('fpsStats')
          document.body.appendChild(stats.domElement)
        } else if (document.getElementsByClassName('fpsStats').length !== 0) {
          stats = undefined
          document.getElementsByClassName('fpsStats')[0].remove()
        }
      }
      animate()
    }
    start()

    function createStats() {
      var stats = new Stats()
      stats.setMode(0)

      stats.domElement.style.position = 'absolute'
      stats.domElement.style.left = '0'
      stats.domElement.style.top = '0'

      return stats
    }
  },
}
</script>

<style></style>
